<template>
  <div>
    <div class="home-title">
      <span>资源管理</span>
    </div>
    <div class="table-content">
      <div class="table-title">
        <a-button type="primary" @click="showModal"> <a-icon type="plus"/> 新建资源</a-button>
        <a-input-search allowClear @blur="onSearch" v-model="searchObj.resourceLabel" @search="onSearch" style="width: 200px; margin: 0 20px" placeholder="请输入资源名称搜索" />
      </div>
      <div class="table-box">
        <a-table :columns="columns"
                 @change="tableChange"
                 :rowKey="(record, index) => {return index}"
                 :pagination="pagination"
                 :data-source="data">
          <span slot="resourceStatus" slot-scope="text">
            <span v-if="text">
               <span class="device-status" :style="{background: '#15C937'}"></span>
              启动</span>
            <span v-else>
               <span class="device-status" :style="{background: '#C91521'}"></span>
              停止</span>
          </span>
          <span slot="action" slot-scope="text, record">
            <span class="table-btn" @click="listDetails(record)">详情</span>
            <span class="table-btn" @click="changeStatus(record)">
              <span v-if="record.resourceStatus">
                停止</span>
              <span v-else>
                启动</span>
            </span>
           <span class="table-btn" @click="editResource(record)">修改</span>
           <a-popconfirm
           title="你确定删除此资源吗?"
           ok-text="确定"
           cancel-text="取消"
           @confirm="deleteCancel(record.id)"
         >
         <span class="table-btn"> 删除 </span>
       </a-popconfirm>
          </span>
        </a-table>
      </div>
    </div>
    <a-modal v-model="visible" :width="800" :title="resTitle" @ok="handleOk" @cancel="closeHand">
      <a-form-model
        layout="horizontal"
        ref="resForm"
        :model="resform"
        :rules="rules"
      >
        <a-form-model-item label="资源类型" prop="resourceType">
          <a-select v-model="resform.resourceType" placeholder="请选择资源类型">
            <a-select-option  v-for="(item, index) in resourceList" :value="item.key" :key="index">
              {{item.title}}
            </a-select-option>
          </a-select>
<!--          <a-button>测试连接</a-button>-->
        </a-form-model-item>
        <a-form-model-item label="资源名称" prop="resourceLabel">
          <a-input v-model="resform.resourceLabel" placeholder="请输入资源名称"></a-input>
        </a-form-model-item>
        <a-divider />
        <div class="form-flex" v-if="resform.resourceType === 'RABBIT'">
          <a-row :gutter="16">
            <a-col :span="12">
              <a-form-model-item label="RabbitMQ服务器地址" prop="host">
                <a-input v-model="resform.host" placeholder="请输入RabbitMQ服务器地址"/>
              </a-form-model-item>
              <a-form-model-item label="用户名" prop="username">
                <a-input v-model="resform.username" placeholder="请输入用户名"/>
              </a-form-model-item>
              <a-form-model-item label="连接超时时间">
                <a-input-number id="input1" style="width: 100%;" v-model="resform.connectTimeout" placeholder="请输入连接超时时间"/>
              </a-form-model-item>
              <a-form-model-item label="心跳间隔">
                <a-input-number id="keepLive" style="width: 100%;" :min="60" v-model="resform.keepLive" placeholder="请输入心跳间隔"/>
              </a-form-model-item>
            </a-col>
            <a-col :span="12">
              <a-form-model-item label="RabbitMQ服务器端口" prop="port">
                <a-input-number id="port" style="width: 100%;" v-model="resform.port" placeholder="请输入RabbitMQ服务器端口"/>
              </a-form-model-item>
              <a-form-model-item label="密码">
                <a-input-password v-model="resform.password" placeholder="请输入密码" />
              </a-form-model-item>
              <a-form-model-item label="连接池大小" prop="connectPoolSize">
                <a-input-number id="connectPoolSize" style="width: 100%;" :min="1000" v-model="resform.connectPoolSize" placeholder="请输入连接池大小" />
              </a-form-model-item>
              <a-form-model-item label="虚拟主机">
                <a-input v-model="resform.virtualHost" placeholder="请输入虚拟主机"/>
              </a-form-model-item>
<!--              <a-form-model-item label="自动重连时间">
                <a-input v-model="form.keepLive" placeholder="请输入自动重连时间"/>
              </a-form-model-item>-->
            </a-col>
          </a-row>
        </div>
        <div class="form-flex" v-if="resform.resourceType === 'KAFKA'">
          <a-row :gutter="16">
            <a-col :span="12">
              <a-form-model-item label="Kafka服务器">
                <a-input v-model="resform.host" placeholder="请输入服务器地址"/>
              </a-form-model-item>
              <a-form-model-item label="Kafka用户名">
                <a-input v-model="resform.username" placeholder="请输入用户名"/>
              </a-form-model-item>
              <a-form-model-item label="异步调用超时时间">
                <a-input-number style="width: 100%;"  id="reqTimeout" :min="1000" v-model="resform.reqTimeout" placeholder="请输入异步调用超时时间 "/>
              </a-form-model-item>
              <a-form-model-item label="压缩类型">
                <a-select v-model="resform.compressionType" placeholder="请选择压缩类型">
                  <a-select-option value="gzip">
                    gzip
                  </a-select-option>
                </a-select>
              </a-form-model-item>
<!--              <a-form-model-item label="查询API版本号">
                <a-select v-model="form.desc" placeholder="请输入">
                  <a-select-option value="shanghai">
                    true
                  </a-select-option>
                </a-select>
              </a-form-model-item>-->
            </a-col>
            <a-col :span="12">
              <a-form-model-item label="Metadata更新间隔">
                <a-input-number style="width: 100%;" id="metadataUpdateTime" :min="60" v-model="resform.metadataUpdateTime" placeholder="请输入Metadata更新间隔"/>
              </a-form-model-item>
              <a-form-model-item label="Kafka密码">
                <a-input-password v-model="resform.password" placeholder="Kafka认证密码"/>
              </a-form-model-item>
              <a-form-model-item label="最大批处理字节数">
                <a-input-number style="width: 100%;"  id="batchSize" :min="0" v-model="resform.batchSize" placeholder="最大批处理字节数"/>
              </a-form-model-item>
              <a-form-model-item label="发送消息的缓冲区大小">
                <a-input-number style="width: 100%;" id="bufferSize" :min="0" v-model="resform.bufferSize" placeholder="发送消息的缓冲区大小"/>
              </a-form-model-item>
<!--              <a-form-model-item label="开启SSL">-->
<!--                <a-input v-model="form.name"/>-->
<!--              </a-form-model-item>-->
            </a-col>
          </a-row>
        </div>
        <div class="form-flex" v-if="resform.resourceType === 'HTTP'">
          <a-form-model-item label="连接超时" prop="connectTimeout">
            <a-input-number style="width: 100%;" :value=10 v-model="resform.connectTimeout" :min="10" placeholder="请输入超时时间"/>
          </a-form-model-item>
          <a-form-model-item label="请求头">
           <div>
             <div class="form-head">
               <div class="form-items">键</div>
               <div class="form-items">值</div>
               <div class="form-add" @click="addHeader">添加</div>
             </div>
             <div class="form-ipt">
               <a-row :gutter="16" v-for="(item, index) in headerArr" :key="index">
                 <a-col :span="10">
                   <a-input v-model="item.key" placeholder="请输入key"/>
                 </a-col>
                 <a-col :span="10">
                   <a-input v-model="item.value" placeholder="请输入value"/>
                 </a-col>
                 <a-col :span="4" style="text-align: center;">
                   <span class="table-btn" @click="deleteHeader(item)">删除</span>
                 </a-col>
               </a-row>
             </div>
           </div>
          </a-form-model-item>
          <a-row :gutter="16">
<!--            <a-col :span="12">
              <a-form-model-item label="请求方法">
                <a-select v-model="form.methods" placeholder="请输入">
                  <a-select-option value="POST">
                    POST
                  </a-select-option>
                  <a-select-option value="GET">
                    GET
                  </a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>-->
            <a-col :span="24">
              <a-form-model-item label="请求URL" prop="host">
                <a-input v-model="resform.host" placeholder="请输入地址"/>
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
      </a-form-model>
    </a-modal>
    <a-modal @ok="detailsHandleOk" v-model="detailVisible" :width="800" title="资源信息">
      <a-descriptions :title="resDetail.resourceLabel">
        <a-descriptions-item label="资源类型">
         {{resDetail.resourceType}}
        </a-descriptions-item>
      </a-descriptions>
      <a-descriptions bordered v-if="resDetail.resourceType === 'HTTP'" :column="2">
        <a-descriptions-item label="超时时间">
          {{resDetail.connectTimeout}}
        </a-descriptions-item>
        <a-descriptions-item label="请求头">
          <p v-for="(item, index) in headerArr" :key="index" style="margin: 0;">
            <span>{{item.key}}:</span>
            <span>{{item.value}}</span>
          </p>
        </a-descriptions-item>
        <a-descriptions-item label="请求URL">
          {{resDetail.host}}
        </a-descriptions-item>
      </a-descriptions>
      <a-descriptions bordered v-if="resDetail.resourceType === 'KAFKA'" :column="2">
        <a-descriptions-item label="kafka服务器">
          {{resDetail.host}}
        </a-descriptions-item>
        <a-descriptions-item label="Metadata更新间隔">
          {{resDetail.metadataUpdateTime}}
        </a-descriptions-item>
        <a-descriptions-item label="Kafka用户名">
          {{resDetail.username}}
        </a-descriptions-item>
        <a-descriptions-item label="Kafka密码">
          {{resDetail.password}}
        </a-descriptions-item>
        <a-descriptions-item label="异步调用超时时间">
          {{resDetail.reqTimeout}}
        </a-descriptions-item>
        <a-descriptions-item label="最大批处理字节数">
          {{resDetail.batchSize}}
        </a-descriptions-item>
        <a-descriptions-item label="压缩类型">
          {{resDetail.compressionType}}
        </a-descriptions-item>
        <a-descriptions-item label="发送消息的缓冲区大小">
          {{resDetail.bufferSize}}
        </a-descriptions-item>
      </a-descriptions>
      <a-descriptions bordered v-if="resDetail.resourceType === 'RABBIT'" :column="2">
        <a-descriptions-item label="RabbitMQ服务器地址">
          {{resDetail.host}}
        </a-descriptions-item>
        <a-descriptions-item label="RabbitMQ服务器端口">
          {{resDetail.port}}
        </a-descriptions-item>
        <a-descriptions-item label="连接超时时间">
          {{resDetail.connectTimeout}}
        </a-descriptions-item>
        <a-descriptions-item label="连接池大小">
          {{resDetail.connectPoolSize}}
        </a-descriptions-item>
        <a-descriptions-item label="用户名">
          {{resDetail.username}}
        </a-descriptions-item>
        <a-descriptions-item label="密码">
          {{resDetail.password}}
        </a-descriptions-item>
        <a-descriptions-item label="心跳间隔">
          {{resDetail.keepLive}}
        </a-descriptions-item>
        <a-descriptions-item label="虚拟主机">
          {{resDetail.virtualHost}}
        </a-descriptions-item>
      </a-descriptions>
    </a-modal>
  </div>
</template>

<script>
import BTextarea from '../../components/BTextarea/BTextarea'
const columns = [
  {
    title: '资源名称',
    key: 'resourceLabel',
    dataIndex: 'resourceLabel'
  },
  {
    dataIndex: 'id',
    title: '资源ID',
    key: 'id'
  },
  {
    title: '资源类型',
    dataIndex: 'resourceType',
    key: 'resourceType'
  },
  {
    title: '状态',
    key: 'resourceStatus',
    dataIndex: 'resourceStatus',
    scopedSlots: { customRender: 'resourceStatus' }
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
export default {
  name: 'resourceAllocationList',
  components: {
    BTextarea
  },
  data () {
    return {
      data: [],
      columns,
      searchObj: {
        resourceLabel: ''
      },
      pagination: {
        showLessItems: true,
        pageSizeOptions: ['10', '20', '30'],
        showSizeChanger: true,
        pageSize: 10,
        current: 1,
        hideOnSinglePage: false,
        total: 0
      },
      visible: false,
      resform: {
        resourceLabel: '',
        resourceType: undefined,
        connectTimeout: 10,
        configuration: {},
        host: '',
        methods: 'POST',
        headers: {},
        port: '',
        connectPoolSize: 1000,
        username: '',
        password: '',
        virtualHost: '',
        keepLive: 0,
        metadataUpdateTime: 60,
        reqTimeout: 1000,
        compressionType: undefined,
        metaUpdateTs: '',
        bufferSize: 0,
        batchSize: 0
      },
      headerArr: [],
      rules: {
        resourceLabel: [{ required: true, message: '请输入资源名称', trigger: 'blur' }],
        resourceType: [
          { required: true, message: '请选择资源类型', trigger: 'blur' }
        ],
        host: [{ required: true, message: '请输入地址', trigger: 'change' }],
        connectTimeout: [
          { required: false, message: '请输入超时时间', trigger: 'change' }
        ],
        password: [
          { validator: this.$validator.checkCode, message: '不可输入中文', trigger: ['blur', 'change'] }
        ]
      },
      resourceList: [
        {
          title: 'RabbitMQ',
          key: 'RABBIT'
        },
        {
          title: 'Kafka',
          key: 'KAFKA'
        },
        {
          title: 'HTTP编码',
          key: 'HTTP'
        }
      ],
      resTitle: '创建资源',
      isEdit: false,
      resInfo: {},
      detailVisible: false,
      resDetail: {}
    }
  },
  created() {
    this.getList()
  },
  methods: {
    /**
     * 切换分页
     * */
    tableChange(pagination) {
      this.pagination.pageSize = pagination.pageSize
      this.pagination.current = pagination.current
      this.pagination.total = pagination.total
      this.getList()
    },
    /**
     * 获取设备列表
     * */
    getList() {
      const pageOption = {
        resourceLabel: this.searchObj.resourceLabel,
        page: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.$api.getResourceList(pageOption).then(res => {
        const resData = res.data
        console.log('设备数据', res)
        this.pagination.total = resData.total
        this.pagination.pageSize = resData.size
        this.pagination.current = resData.current
        const resRecords = resData.records
        this.data = resRecords
      })
    },
    /**
     * HTTP添加请求头
     * */
    addHeader() {
      let obj = {
        key: '',
        value: ''
      }
      this.headerArr.push(obj)
    },
    /**
     * 请求头删除
     * */
    deleteHeader(val) {
      this.headerArr.map((item, index) => {
        if (item === val) {
          this.headerArr.splice(index, 1)
        }
      })
    },
    showModal() {
      this.visible = true
      this.isEdit = false
      this.resTitle = '创建资源'
      this.resform = {
        connectTimeout: 10,
        methods: 'POST',
        keepLive: 0,
        metadataUpdateTime: 60,
        bufferSize: 0,
        batchSize: 0
      }
      this.headerArr = []
    },
    handleOk() {
      if (this.isEdit) {
        this.putReaource()
      } else {
        this.postAddResource()
      }
    },
    editResource(val) {
      this.visible = true
      this.isEdit = true
      this.resTitle = '修改资源'
      this.getInfo(val.id)
      setTimeout(() => {
        console.log('数组1', this.resInfo)
        this.resform = Object.assign({}, this.eencapsulationFormDetail(this.resform, this.resInfo))
      }, 500)
    },
    closeHand() {
      this.visible = false
      this.$refs['resForm'].resetFields()
    },
    /**
     * 增加资源
     * */
    postAddResource() {
      let formObj = {
        resourceLabel: '',
        resourceType: undefined,
        configuration: {}
      }
      this.encapsulationForm(formObj)
      this.$refs.resForm.validate(valid => {
        if (valid) {
          this.$api.postResource(formObj).then(res => {
            this.visible = false
            this.getList()
          })
        }
      })
    },
    /**
     * 封装表单
     * */
    encapsulationForm(formObj) {
      switch (this.resform.resourceType) {
        case 'HTTP':
          let obj = {}
          this.headerArr.map(items => {
            obj[items.key] = items.value
            console.log('键值对', obj)
          })
          this.resform.headers = obj
          Object.assign(formObj, {
            resourceType: this.resform.resourceType,
            connectTimeout: this.resform.connectTimeout,
            configuration: {
              resourceType: this.resform.resourceType,
              host: this.resform.host,
              headers: this.resform.headers
            },
            resourceLabel: this.resform.resourceLabel
          })
          break
        case 'RABBIT' :
          Object.assign(formObj, {
            resourceType: this.resform.resourceType,
            connectTimeout: this.resform.connectTimeout,
            resourceLabel: this.resform.resourceLabel,
            configuration: {
              resourceType: this.resform.resourceType,
              host: this.resform.host,
              port: this.resform.port,
              connectPoolSize: this.resform.connectPoolSize,
              username: this.resform.username,
              password: this.resform.password,
              connectTimeout: this.resform.connectTimeout,
              virtualHost: this.resform.virtualHost,
              keepLive: this.resform.keepLive
            }
          })
          break
        case 'KAFKA':
          Object.assign(formObj, {
            resourceType: this.resform.resourceType,
            resourceLabel: this.resform.resourceLabel,
            configuration: {
              resourceType: this.resform.resourceType,
              host: this.resform.host,
              metadataUpdateTime: this.resform.metadataUpdateTime,
              reqTimeout: this.resform.reqTimeout,
              connectTimeOut: this.resform.connectTimeOut,
              compressionType: this.resform.compressionType,
              metaUpdateTs: this.resform.metaUpdateTs,
              bufferSize: this.resform.bufferSize,
              username: this.resform.username,
              password: this.resform.password,
              batchSize: this.resform.batchSize
            }
          })
          break
      }
      return formObj
    },
    /**
     * 封装表单回显
     * */
    eencapsulationFormDetail(detailForm, resInfo) {
      console.log('这里有没有', detailForm)
      switch (resInfo.resourceType) {
        case 'HTTP':
          let arr = []
          let Obj = resInfo.configuration.headers
          for (let propery in Obj) {
            let aObj = {}
            Object.assign(aObj, {
              key: propery,
              value: Obj[propery]
            })
            arr.push(aObj)
          }
          this.headerArr = arr
          console.log('数组', arr)
          Object.assign(detailForm, {
            resourceType: resInfo.resourceType,
            resourceLabel: resInfo.resourceLabel,
            host: resInfo.configuration.host,
            headers: this.headerArr
          })
          break
        case 'KAFKA':
          Object.assign(detailForm, {
            resourceType: resInfo.resourceType,
            resourceLabel: resInfo.resourceLabel,
            host: resInfo.configuration.host,
            username: resInfo.configuration.username,
            password: resInfo.configuration.password,
            metadataUpdateTime: resInfo.configuration.metadataUpdateTime,
            reqTimeout: resInfo.configuration.reqTimeout,
            connectTimeOut: resInfo.configuration.connectTimeOut,
            compressionType: resInfo.configuration.compressionType,
            metaUpdateTs: resInfo.configuration.metaUpdateTs,
            bufferSize: resInfo.configuration.bufferSize,
            batchSize: resInfo.configuration.batchSize
          })
          break
        case 'RABBIT':
          Object.assign(detailForm, {
            resourceType: resInfo.resourceType,
            resourceLabel: resInfo.resourceLabel,
            host: resInfo.configuration.host,
            port: resInfo.configuration.port,
            connectPoolSize: resInfo.configuration.connectPoolSize,
            username: resInfo.configuration.username,
            password: resInfo.configuration.password,
            connectTimeout: resInfo.configuration.connectTimeout,
            virtualHost: resInfo.configuration.virtualHost,
            keepLive: resInfo.configuration.keepLive
          })
          break
      }
      return detailForm
    },
    /**
     * 修改资源信息
     * */
    putReaource() {
      let formObj = {
        id: this.resInfo.id,
        resourceLabel: '',
        resourceType: '',
        configuration: {},
        description: ''
      }
      this.encapsulationForm(formObj)
      this.$refs.resForm.validate(valid => {
        if (valid) {
          this.$api.putResourceInfo(formObj).then(res => {
            this.visible = false
            this.getList()
          })
        }
      })
    },
    onSearch() {
      this.getList()
    },
    /**
     * 获取资源信息
     * */
    getInfo(id) {
      this.$api.getResourceInfo(id).then(res => {
        console.log('获取详情', res)
        this.resInfo = res.data
      })
    },
    /**
     * 修改资源状态
     * */
    changeStatus(record) {
      const obj = {
        resourceId: record.id,
        resourceStatus: !record.resourceStatus
      }
      this.$api.putResourceStatus(obj).then(res => {
        this.getList()
      })
    },
    /**
     * 查看详情
     * */
    listDetails(record) {
      this.detailVisible = true
      this.getInfo(record.id)
      setTimeout(() => {
        this.resDetail = this.resInfo
        this.eencapsulationFormDetail(this.resDetail, this.resInfo)
        console.log('数组1', this.resDetail)
      }, 500)
    },
    detailsHandleOk() {
      this.detailVisible = false
    },
    /**
     * 删除资源
     * */
    deleteCancel(id) {
      this.$api.deleteResource(id).then(res => {
        this.getList()
      })
    },
    goDetail () {
      this.$router.push({path: '/productDetatils'})
    }
  }
}
</script>

<style lang="scss" scoped>
.product-info {
  background: #ffffff;
  margin: 24px;
  .product-content {
    padding: 24px;
    color: #8a8e99;
  }
}
.form-head {
  display: flex;
  justify-content: space-between;
  background: #d9ddde;
  align-items: center;
  text-align: center;
  .form-items {
    flex: 1;
  }
  .form-add {
    width: 130px;
    color: #0D53BF;
  }
}
.form-ipt {
  width: 100%;
}
